import AudioReactRecorder, { RecordState } from "audio-react-recorder";
import { useState } from "react";

function AudioRecorder(props) {
  const [record, setRecord] = useState(null);
  const [audioData, setAudioData] = useState(null);
  const [btnActive, setBtnActive] = useState();

  const recordStart = () => {
    setRecord(RecordState.START);
  };

  const recordPause = () => {
    setRecord(RecordState.PAUSE);
  };

  const recordStop = () => {
    setRecord(RecordState.STOP);
  };

  const onStop = (data) => {
    setAudioData(data);
    console.log(data);
  };

  return (
    <div>
      <AudioReactRecorder
        state={record}
        onStop={onStop}
        backgroundColor="black"
        foregroundColor="gold"
        canvasWidth="500"
        canvasHeight="300"
      />
      {audioData ? (
        <p className="fileSize">
          Record File Size :&nbsp;
          {audioData
            ? (audioData.blob.size / (1024 * 1024)).toFixed(2)
            : null}{" "}
          MB
        </p>
      ) : null}
      {audioData ? <audio id="audio" controls src={audioData.url} /> : null}
      <div className="recorder-btn">
        <button
          className={`w-btn-neon2 ${btnActive === "play" ? "btn-active" : ""}`}
          onClick={() => {
            recordStart();
            setBtnActive("play");
          }}
        >
          <i className="fa-solid fa-microphone"></i>
        </button>
        <button
          className={`w-btn-neon2 ${btnActive === "pause" ? "btn-active" : ""}`}
          onClick={() => {
            recordPause();
            setBtnActive("pause");
          }}
        >
          <i className="fa-solid fa-pause"></i>
        </button>
        <button
          className={`w-btn-neon2 ${btnActive === "stop" ? "btn-active" : ""}`}
          onClick={() => {
            recordStop();
            setBtnActive("stop");
          }}
        >
          <i className="fa-solid fa-stop"></i>
        </button>
        {audioData ? (
          <a download href={audioData.url}>
            <button className="w-btn-neon2">
              <i className="fa-solid fa-download"></i>
            </button>
          </a>
        ) : null}
      </div>
    </div>
  );
}

export default AudioRecorder;
